import cls from 'classnames'
import React, { memo } from 'react'

import SORTABLE_CONFIG from '../config/sortable'
import AudioComponent from "./AudioComponent"
import PlayListComponent from "./PlayListComponent";
import PLAY_LIST_MODE from "../config/playListModes";

var newAudioList=[];
const AudioListsPanel = ({
/*AudioListPanel*******************************************************************/
panelToggleAnimate,         //css class
glassBg,                    //css class
isMobile,                   //css class
listMode,                   //ListPanel ('list' : playlist, 'music' : audioList)

/*******************************************************************************/
locale,                     //
icon,                       //icon 
remove,                     //
onClosePanel,               //AudioListPanel  

/*PlayListComponent****************************************************************/
playLists,                  //  
onPlayListClick,            //   
onChangeAddPlayListName,    //  ,    (onChange)
onAddPlayList,              //    (onClick)
onAddPlayListConfirm,       //    (onClick)
isAddPlayList,              //   
addPlayListName,            //   
                             onPlayListRename,
                             onPlayListDuplicate,
                             onPlayListDelete,
                             playListRenameInput,

/*AudioComponent*******************************************************************/
audioLists,                 //
playing,                    // 
playId,                     //  id
onPlay,                     //  
removeId,                   //  id
onDelete,                   //   (onClick)
loading,                    // 
playListName,               //  
onBackClick,                // (onClick)(   )
onLikeModeChange            //like  (onClick)
}) => {
  return (
      <div
          className={cls('audio-lists-panel', panelToggleAnimate, {
            'audio-lists-panel-mobile': isMobile,
            'glass-bg': glassBg,
          })}
      >
        {/*////////////////Header///////////////////////*/}
        {listMode === PLAY_LIST_MODE.list ?
            (
                <PlayListComponent
                    playLists = {playLists}
                    icon = {icon}
                    locale = {locale}
                    remove = {remove}
                    onClosePanel = {onClosePanel}
                    onAddPlayList = {onAddPlayList}
                    onPlayListClick={onPlayListClick}
                    onChangeAddPlayListName = {onChangeAddPlayListName}
                    onAddPlayListConfirm={onAddPlayListConfirm}
                    isAddPlayList = {isAddPlayList}
                    addPlayListName = {addPlayListName}
                    onPlayListRename={onPlayListRename}
                    onPlayListDuplicate={onPlayListDuplicate}
                    onPlayListDelete={onPlayListDelete}
                    playListRenameInput={playListRenameInput}
                />
            )
            :
            (
                <AudioComponent
                    audioLists = {audioLists}
                    locale = {locale}
                    icon = {icon}
                    playing = {playing}
                    playId = {playId}
                    onPlay = {onPlay}
                    removeId = {removeId}
                    remove = {remove}
                    onDelete = {onDelete}
                    loading = {loading}
                    playListName = {playListName}
                    onClosePanel = {onClosePanel}
                    onBackClick = {onBackClick}
                    onLikeModeChange = {onLikeModeChange}
                />
            )
        }




      </div>

  )
}

export default memo(AudioListsPanel)
